@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #f0f0f5;
  -webkit-font-smoothing: antialiased;
}

input,
button,
textarea {
  font: 400 18px Roboto, sans-serif;
}

button {
  cursor: pointer;
}

.HeaderContainer {
  background-color: #084387;
  height: 95px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.HeaderContainer button {
  background-color: transparent;
  border-color: transparent;
  display: flex;
  align-items: center;
}

.HeaderContainer h1 {
  color: #ffff;
  font-weight: bold;
  font-size: 30px;
}

.IconContainer {
  margin-left: 6px;
  height: 33px;
  display: flex;
  align-items: flex-end;
}

.Container {
  width: 100%;
  max-width: 1120px;
  height: 100vh;
  margin: 0 auto;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

